ul.menu { padding: 0; }

ul.menu li{
    list-style: none;
    display: inline-block;
    padding: 0 20px;
}

ul.menu li a{
    text-decoration: none;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.6);
    font-weight: 600;
}

.producers{
    position: relative;
    float: left;
    width: 100%;
}

.producers .section {
    position: relative;
    float: left;
    width: 100%;
}

.producers  .fullWidth {
    position: relative;
    float: left;
    width: 100%;
}

.producers h2{ font-size: 60px; }
.producers p{
  font-size: 18px;
  line-height: 1.8;
}

.producers .bg-grey { background: #EAEAEA;  }


.producers .header img{
    margin-bottom: 120px;
}

.producers .header p{
    color: #fff;
    font-size: 26px;
    text-align: center;
}

.producers .header{
    padding: 57px 0;
    background: url(https://modshow.tv/wp-content/uploads/2019/04/modshow-premium-bg.jpg) center top no-repeat;
    background-color: rgba(12,12,12,0.76) !important;
    background-blend-mode: overlay;
}

.producers .header .heroTitle{
    color: #fff;
    margin: 20px auto;
    font-weight: 600;
    font-size: 60px;
}

.producers  .header p{
    color: #fff;
    font-size: 26px;
    text-align: center;
}

.producers  .icons {
  background: #fff;
  padding: 57px 0;
}
.producers h2{
    font-size: 60px;
}

.producers  .icons .column{
    position: relative;
    float: left;
    width: 30%;
    /* margin-right: 5.5%;*/
}

.producers  .icons .column.center{ margin: 0 5%; }

.producers  .icons .column h3{
  font-size: 30px;
  margin-bottom: 26px;

}

.producers p{
  font-size: 18px;
  line-height: 1.8;
}

.producers .look { background: #EAEAEA; }

.producers .section .columnHalf{
    position: relative;
    float: left;
    width: 50%;
    min-height: 1px;
}

.producers .believe{
    padding: 57px 0;
    background-image: url(https://modshow.tv/wp-content/uploads/2019/04/Business-Profit-Accelerator-bg-man-mobile-technology.jpg);
    background-size: cover;
    background-position: center;
    background-color: rgba(12,12,12,0.67) !important;
    color: #fff;
}


.producers .income .containerFull{
    position: relative;
    width: 80%;
    margin: 0 auto;
    text-align: center;
}

.producers .income .containerFull img {
    width: 100%;
    height: auto;
}

.producers .coming{
    background: url(https://modshow.tv/wp-content/uploads/2019/04/modshow-merchandise-sales.jpg);
    background-size: cover;
    background-position: center;
    background-color: rgba(12,12,12,0.67) !important;
    color: #fff;
    padding-top: 140px;
    padding-bottom: 190px;
}


.terms { text-align: left;}
.terms header,
footer{
    position: relative;
    float: left;
    width: 100%;
    box-shadow: 0 0 7px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.terms ul li,
.terms ol li{
    font-weight: 600;
    font-size: 18px;
}



.terms .content {
    position: relative;
    float: left;
    width: 100%;
    padding-top: 50px;
}

.terms .content p{
    font-size: 18px;
    color: #000;
    font-weight: 600;
}

.terms footer{
  box-shadow: none;
}

@media all and ( max-width: 479px ){
    .terms  .content { padding: 0 15px; width: auto; }
    .producers .header img { margin-bottom: 20px; }
    .producers .header .heroTitle{ font-size: 24px;  }
    .producers .header p { font-size: 17px; padding: 0 10px;}
    .producers .header p br { display: none; }

    .producers .section .container,
    .producers .coming.section { padding: 0 20px; }
    .producers .section .container h2 { font-size: 26px; }

    .producers .icons .column,
    .producers .section .container .columnHalf,
    .producers .section .containerFull .columnHalf{ width: 100%; }
    .producers .icons .column.center{ margin: 0 }


    .producers .section .container .columnHalf { margin: 10px 0; padding: 0; }
    .producers .section .container .columnHalf h3 { font-size: 26px; text-align: center; }

}
